/* poppins-regular - latin */
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/poppins-v5-latin-regular.eot"); /* IE9 Compat Modes */
  src: local("Poppins Regular"), local("Poppins-Regular"),
    url("../fonts/poppins-v5-latin-regular.eot?#iefix")
      format("embedded-opentype"),
    /* IE6-IE8 */ url("../fonts/poppins-v5-latin-regular.woff2") format("woff2"),
    /* Super Modern Browsers */ url("../fonts/poppins-v5-latin-regular.woff")
      format("woff"),
    /* Modern Browsers */ url("../fonts/poppins-v5-latin-regular.ttf")
      format("truetype"),
    /* Safari, Android, iOS */
      url("../fonts/poppins-v5-latin-regular.svg#Poppins") format("svg"); /* Legacy iOS */
}
/* ibm-plex-mono-regular - latin */
@font-face {
  font-family: 'IBM Plex Mono';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/ibm-plex-mono-v3-latin-regular.eot'); /* IE9 Compat Modes */
  src: local('IBM Plex Mono'), local('IBMPlexMono'),
       url('../fonts/ibm-plex-mono-v3-latin-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('../fonts/ibm-plex-mono-v3-latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('../fonts/ibm-plex-mono-v3-latin-regular.woff') format('woff'), /* Modern Browsers */
       url('../fonts/ibm-plex-mono-v3-latin-regular.ttf') format('truetype'), /* Safari, Android, iOS */
       url('../fonts/ibm-plex-mono-v3-latin-regular.svg#IBMPlexMono') format('svg'); /* Legacy iOS */
}

/* password-font */

@font-face {
  font-family: 'password';
  src: url('../fonts/password.woff2') format('woff2'),
       url('../fonts/password.woff') format('woff'),
       url('../fonts/password.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

