/* DASHBOARD */
.dashboard {
  
}
.line-img {
  /* position: fixed; */
  z-index: -1;
  position: absolute;
  width: 1400px;
}
/* INTRO */
.intro {
  padding: 3rem 3rem 3rem 3rem;
  margin: 0;
  min-height: 100vh;
  /* display: flex; */
  /* flex-wrap: wrap; */
  /* align-content: center; */
}
.intro-heading > p {
  margin: 0;
  font-size: 5em;
  font-weight: 800;
  padding: 0;
  /* font-size: px; */
}
.intro-text {
    width: 50%;
}
.intro-text>p {
    text-align: left;
    font-size: 20px;
}
.intro-text > div > p {
  margin: 0;
  font-size: 20px;
}
.join-btn:hover {
    color: #fff !important;
    background-color: #7926f6 !important;
}
.join-btn {
  text-transform: none !important;
  border: 3px solid #7926f6 !important;
  border-radius: 50px !important;
  color: #7926f6 !important;
  width: 9rem;
  font-weight: bold !important;
  margin-top: 2rem !important;
  font-family: "Poppins", sans-serif !important;
  font-size: 18px !important;
  background-color: #fff !important;
}

.img-search-grid {
    text-align: center;
}
.intro-img-search {
    width: 40em;
    /* text-align: right; */
    /* float: right; */
    /* margin: 0 auto; */
    /* text-align: center; */
}
/* TIMELINE */
.timeline {
  /* width: 70%; */
  margin: 0 auto;
  /* background-color: rgba(0,0,0,.08); */
  /* min-height: 100vh; */
}
.timeline>div::before {
  background: #c2c2c2; 
}
.timeline>div>div>div>span {
  box-shadow: none;
  /* border: 4px sol */
  box-shadow: 0 0 0 4px #ddd, inset 0 2px 0 rgba(0,0,0,.08), 0 3px 0 4px rgba(0,0,0,.05);
}
.timeline>div>div>div>div {
  box-shadow:0 0 20px #ddd
}

/* WORKSPACE */
.workspace {
  padding: 5rem 3rem;
  min-height: 80vh;
  display: flex;
  align-items: center;
}

/* Console */

.console-workspace-xs {
  display: none;
}

.section-console {

}
.console-top {
  background-color: grey;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2px 1rem;
  border-radius: 5px 5px 0 0;
}
.console {
  height: 590px;
    /* max-height: 300px; */
  /* height: 300px; */
  /* overflow-y: auto; */
  overflow-x: hidden;
  overflow-y: auto;
  cursor: text;
  padding: 1rem 1rem 0rem 0rem;
  font-family: 'IBM Plex Mono';
  /* animation:  */
  /* typing 3.5s steps(40, end), */
  /* blink-caret .7s step-end infinite; */
  border-radius: 0 0 5px 5px;
}
.console::-webkit-scrollbar {
  width: 10px;
}
.console::-webkit-scrollbar-thumb {
  background-color: #7926f6;
  cursor: pointer !important;
}
.console::-webkit-scrollbar-thumb:hover {
  opacity: 0.9;
}
.line-text {
  overflow-x: hidden;
}
#line {
  
}
.line {
  margin: 0px;
  margin-right: 0.1rem;
  padding: 0 0 0 1rem;
}
.password {
  /* -webkit-text-security: disc;
  -moz-text-security: disc;
  -text-security: disc; */
  /* height:20px; width:100px; */
  /* -webkit-appearance: textfield; */
  /* padding: 1px; */
  /* background-color: white; */
  /* border: 2px inset; */
  /* -webkit-user-select: text; */
  font-family: 'password';
  display: flex;
  align-items: center;
  /* cursor: auto; */
}

/* Workspace-right-text */
.workspace-right-text {
  width: 50%;
  /* float: right; */
  margin: 0 auto;
  min-height: 600px;
  display: flex;
  flex-flow: column;
  justify-content: space-between;
}
.workspace-right-text>img {
  width: 20em;
}
.help-text {
  font-family: 'IBM Plex Mono';
  background-color: rgba(0,0,0,0.08);
  padding: 4px 1rem;
}

/* FOOTER */
.footer {
  background-color: #7926f6;
  padding: 3px 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.footer-img {
  width: 2em;
  margin-right: 8px;
}

/* selectdomain */
.select-domain {
  padding: 4rem;
}
.domain-radio label{
  width: fit-content;
}
.domain-radio>label>span>span {
  color: #7926f6 !important;
}
.terms-img {
  width: 45em;
  margin-top: 8em;
  float: right;
}

/* snackbar */

.snackbar-error>div {
  background-color: #e01818 !important;
}
.snackbar-success>div {
  background-color: green !important;
}

/* question */
.show-question {
  padding: 2rem 8rem;
}
.quiz>p {
  font-size: 20px;
}
.save-btn {
  text-transform: none !important;
  border: 3px solid #7926f6 !important;
  border-radius: 50px !important;
  color: #7926f6 !important;
  /* width: 9rem; */
  height: 2rem;
  padding: 0 3.5rem !important;
  font-weight: bold !important;
  font-family: "Poppins", sans-serif !important;
  font-size: 18px !important;
  background-color: #fff !important;
  margin-top: .8rem !important;
}
.save-btn:hover {
  color: #fff !important;
  background-color: #7926f6 !important;
}
.subjective, .objective {
  background-color: #f7f7f7;
  padding: 3rem 5rem;
  margin-bottom: 4rem;
  border-radius: 5px;
}
.star-component i {
  font-size: 3rem;
}
.answer {
  width: 100%;
  padding: 2rem;
}
.answer>div {
  height: 300px !important;
  align-items: unset !important;
  display: block !important;
}
.answer::-webkit-scrollbar {
  width: 10px;
}
.answer::-webkit-scrollbar-thumb {
  background-color: #7926f6;
  cursor: pointer !important;
}
.answer::-webkit-scrollbar-thumb:hover {
  opacity: 0.9;
}
.register-btn {
  margin-top: 0px !important;
}

@media only screen and (max-width: 1500px) {
  .img-search-grid {
    text-align: center;
  }
  .intro-text {
    width: 65%;
  }
  .line-img {
    width: 1200px;
  }
  .console {
    height: 500px;
  }
  .workspace-right-text {
    min-height: 540px;
  }
  .terms-img {
    width: 35em;
    margin-top: 8em;
  }
}

@media only screen and (max-width: 1400px) {
  .intro-img-search {
    width: 35em;
    /* height: 30em; */
  }
}

@media only screen and (max-width: 1300px) {
  .intro-img-search {
