    font-size: 3rem;
  }
  .intro-text>p,.intro-text>div>p, .quiz>p {
    font-size: 17px;
  }
  .join-btn {
    font-size: 16px !important;
  }
  .terms-img {
    width: 26em;
  }
  .show-question {
    padding: 2rem 5rem;
  }
  .subjective, .objective {
    padding: 3rem 4rem;
  }
}

@media only screen and (max-width: 959px) {
  .line-img {
    display: none;
  }
  .workspace-right-text {
    min-height: unset;
  }
  .intro-text {
    width: 80%;
  }
  .img-search-grid {
    text-align: center;
  }
  .workspace {
    padding: 4rem;
    /* display: flex; */
    /* flex-wrap: wrap-reverse; */
  }
  .intro-img-search {
    width: 60%;
  }
  .console-workspace-xs {
    display: block;
  }
  .console-workspace-lg {
    display: none;
  }
  .workspace-right-text>img {
    display: none;
  }
  .terms-img {
    float: unset;
    margin: 0 auto;
    margin-top: 2rem;
  }
  .terms-img-grid {
    text-align: center;
    /* mar */
  }
}

@media only screen and (max-width: 700px) {
  .intro-img-search {
    /* margin-top: -50px; */
  }
  .workspace {
    padding: 2rem;
  }
  .workspace-right-text {
    min-height: unset;
    margin-bottom: 2rem;
  }
  .show-question {
    padding: 2rem 3rem ;
  }
  .subjective, .objective {
    padding: 2rem 3rem;
  }
  .star-component i {
    font-size: 2rem;
  }
}

@media only screen and (max-width: 600px) {
  .intro-text {
    width: 100%;
  }
  .intro-img-search {
    width: 100%;
    /* margin-top: -100px; */
  }
  .workspace-right-text {
    width: 80%;
    /* margin-top: -100px; */
  }
  .intro-text>p,.intro-text>div>p {
    font-size: 15px;
  }
  .join-btn {
    font-size: 14px !important;
  }
  .subjective, .objective {
    padding: 2rem;
  }
}

@media only screen and (max-width: 500px) {
  .intro {
    padding: 1rem 2rem;
  }
  .intro-img-search {
    /* margin-top: -100px; */
  }
  .console {
    height: 400px;
  }
  .workspace {
    padding: 1rem;
  }
  .join-btn-div {
    text-align: center;
  }
  .select-domain {
    padding: 2rem;
  }
  .terms-img {
    width: 20em;
  }
  .show-question {
    padding: 0;
  }
  .quiz {
    padding: 1rem;
  }
  .quiz>p.f-bold {
    width: 100%;
  }
  .quiz>.save-btn {
    float: right;
  }
  .subjective, .objective {
    padding: 1.5rem;
    margin-bottom: 2rem;
  }
  .home-redirect {
    margin-left: .5rem;
  }
  .star-component i {
    font-size: 1.5rem;
  }
  html,body {
    font-size: 14px;
  }
}

@media only screen and (max-width: 400px) {
  .intro-heading>p {
    font-size: 2.5rem;
  }
}